.card {
  border-radius: 10%;
  width: 85%;
  height: 0;
  padding-bottom: 85%;
  float: none;
  margin: 0 auto 3rem auto;
  border: 3px outset;
  -webkit-box-shadow:0 4px 8px 0 rgba(0, 0, 0, 0.5), 0 6px 20px 0 rgba(0, 0, 0, 0.5);
          box-shadow:0 4px 8px 0 rgba(0, 0, 0, 0.5), 0 6px 20px 0 rgba(0, 0, 0, 0.5);
}

.card:active {
  -webkit-box-shadow:0 2px 4px 0 rgba(0, 0, 0, 0.4), 0 3px 10px 0 rgba(0, 0, 0, 0.4);
          box-shadow:0 2px 4px 0 rgba(0, 0, 0, 0.4), 0 3px 10px 0 rgba(0, 0, 0, 0.4);
  -webkit-transform: scale(.95);
      -ms-transform: scale(.95);
          transform: scale(.95);
  -webkit-transition: .1s ease;
  -o-transition: .1s ease;
  transition: .1s ease;
}


