.btn-group {
	border: 3px inset;
	border-radius: 9px;
}

.btn {
	font-family: 'Graduate', cursive;
	opacity: 1.0 !important;
}

.btn:focus { 
	outline: none !important; 
}

.btn-primary {
	-webkit-box-shadow: inset #006 0 -1px 9px;
	        box-shadow: inset #006 0 -1px 9px;
}

.btn-primary:hover {
	background-color: #24E0FF !important;
	-webkit-box-shadow: rgba(0, 0, 0, 0.2) 0 -1px 7px 1px, inset #006 0 -1px 9px, #3F8CFF 0 2px 14px !important;
	        box-shadow: rgba(0, 0, 0, 0.2) 0 -1px 7px 1px, inset #006 0 -1px 9px, #3F8CFF 0 2px 14px !important;
	-webkit-transition: .1s ease;
	-o-transition: .1s ease;
	transition: .1s ease;
}

.btn-primary:disabled {
	background-color: #24E0FF;
	-webkit-box-shadow: rgba(0, 0, 0, 0.2) 0 -1px 7px 1px, inset #006 0 -1px 9px, #3F8CFF 0 2px 14px;
	        box-shadow: rgba(0, 0, 0, 0.2) 0 -1px 7px 1px, inset #006 0 -1px 9px, #3F8CFF 0 2px 14px;
	-webkit-transition: .1s ease;
	-o-transition: .1s ease;
	transition: .1s ease;
}

.btn-danger {
  	-webkit-box-shadow: inset #441313 0 -1px 9px;
  	        box-shadow: inset #441313 0 -1px 9px;
}

.btn-danger:hover {
	background-color: #F00 !important;
  	-webkit-box-shadow: rgba(0, 0, 0, 0.2) 0 -1px 7px 1px, inset #441313 0 -1px 9px, rgba(255, 0, 0, 0.5) 0 2px 12px !important;
  	        box-shadow: rgba(0, 0, 0, 0.2) 0 -1px 7px 1px, inset #441313 0 -1px 9px, rgba(255, 0, 0, 0.5) 0 2px 12px !important;
	-webkit-transition: 1s ease;
	-o-transition: 1s ease;
	transition: 1s ease;
}

.btn-danger:disabled {
	background-color: #F00;
  	-webkit-box-shadow: rgba(0, 0, 0, 0.2) 0 -1px 7px 1px, inset #441313 0 -1px 9px, rgba(255, 0, 0, 0.5) 0 2px 12px;
  	        box-shadow: rgba(0, 0, 0, 0.2) 0 -1px 7px 1px, inset #441313 0 -1px 9px, rgba(255, 0, 0, 0.5) 0 2px 12px;
}


