h3 {
  margin: 15px 0px 20px 0px !important;
}

.correct {
	color: #ABFF00;
	text-shadow: -2px 0 #000000, 0 2px #000000, 2px 0 #000000, 0 -2px #000000;
	-webkit-transition: .15s ease;
	-o-transition: .15s ease;
	transition: .15s ease;
}

.incorrect {
	color: #FF0000;	
	text-shadow: -2px 0 #000000, 0 2px #000000, 2px 0 #000000, 0 -2px #000000;
	-webkit-transition: .15s ease;
	-o-transition: .15s ease;
	transition: .15s ease;
}

.default {
	font-weight: bold;
	-webkit-transition: .15s ease;
	-o-transition: .15s ease;
	transition: .15s ease;
}


